main {
  section {
    min-height: 100vh;
    min-height: 100dvh;
    padding: 1rem;
  }

  overflow-x: hidden;

  section#technologies {
    background: #ddd;

    // --tech-backgroud: hsl(203, 100%, 66%);
    --tech-backgroud: hsl(228, 80%, 20%);
    color: #fff;
    background: var(--tech-backgroud);

    max-width: 100vw;

    h1.heading {
      position: relative;
      transform: translateY(-120px);
      z-index: 1;
    }

    .effect {
      position: relative;
      transform: translate(-1.5rem, -44%);
      overflow: hidden;
      width: 100vw;
      height: 20dvh;

      z-index: 1;
      background: black;
      border-top-left-radius: 40%;
      border-top-right-radius: 40%;
      background: linear-gradient(
        to right,
        rgba(33, 13, 22, 1) 16%,
        rgba(184, 40, 105, 1),
        rgba(229, 9, 20, 1),
        rgba(184, 40, 105, 1),
        rgba(33, 13, 22, 1) 84%
      );
      padding: 1rem;

      span {
        position: absolute;
        top: 3%;
        left: 0;
        width: 100%;
        height: 100%;
        border-top-left-radius: 45%;
        border-top-right-radius: 45%;
        background: var(--tech-backgroud);
        z-index: 1;
        overflow: hidden;

        &::before {
          content: "";
          position: absolute;
          top: -160%;
          left: 0;
          width: 100%;
          background: radial-gradient(
            rgba(0, 105, 253, 0.559),
            rgba(0, 119, 255, 0.082),
            transparent,
            transparent
          );
          height: 300%;
          z-index: 2;
        }
      }
    }

    div.heading-flex {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 0.5rem;
      margin-bottom: 2rem;

      h1 {
        display: flex;
        gap: 0.7rem;
        align-items: center;
        margin-left: 10px;
      }

      span {
        position: relative;

        input#search-box {
          padding: 1rem 1.6rem;
          border-radius: 50px;
          border: none;

          width: 100%;
          min-width: 55vw;
          font-size: 1rem;

          outline-color: #806dff;
        }
        i {
          position: absolute;
          right: 0;
          top: 0;
          height: 100%;

          cursor: pointer;

          width: 70px;

          display: flex;
          align-items: center;
          justify-content: center;
          color: #fff;
          background: #806dff;
          border-radius: 0px 50px 50px 0;

          &:hover {
            background: #4073ff;
          }
        }
      }
    }

    .tech {
      padding: 0.3rem;
      padding-top: 1rem;
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      gap: 2rem;

      &.home {
        position: relative;
        transform: translateY(-100px);
        z-index: 1;
      }

      .language-box {
        background: rgb(255, 255, 255) !important;
        border: 1px solid #0ee7d5;
        border-radius: 8px;
        padding: 2rem 1.5rem;
        text-align: center;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        transition: transform 0.3s ease, box-shadow 0.3s ease;

        color: #0ee7d5;

        text-decoration: none;
        cursor: pointer;

        &:hover {
          transform: translateY(-5px);
          box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
          background: #fff;

          h3 {
            color: #00ccf0;
            color: var(--tech-color) !important;
          }

          p {
            color: #121a2b !important;
          }
        }

        img {
          width: 100px;
          object-fit: cover;
          margin-bottom: 10px;
        }

        h3 {
          margin: 0;
          margin-top: 1rem;
          font-size: 18px;
          color: #00ab9d !important;
        }
        p {
          color: #464646c1 !important;
        }
      }
    }
  }

  section#resources {
    padding: 1.5rem;
    margin-top: 100px;

    h1 {
      display: flex;
      gap: 0.5rem;
      align-items: center;

      i {
        cursor: pointer;
        font-size: 2rem;
      }
    }

    ul {
      list-style: none;

      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
      padding: 0;
      gap: 1.5rem;
      margin-top: 1.2rem;
      width: fit-content;
      max-width: 100%;

      li {
        background: rgb(233, 237, 237);
        padding: 1rem;
        max-width: 400px !important;
        border-radius: 10px;
        transition: 0.2s all;

        box-shadow: 2px 2px 7px #313f626b;

        &:hover {
          scale: 1.01;
          background: rgb(193, 229, 255);
        }

        a {
          text-decoration: none;
          color: #000;
          display: flex;
          flex-direction: column;
          gap: 1rem;

          h3 {
            margin: 0;
            color: #ffffff;
            background: rgba(145, 53, 98, 0.826);
            font-size: 1rem;
            max-width: fit-content;

            padding: 0.3rem 0.6rem;
            border-radius: 10px;
          }

          img.thumbnail {
            width: 100%;
            border-radius: 10px;
          }

          .author_info {
            display: flex;
            gap: 0.5rem;
            align-items: center;

            img {
              border-radius: 50%;
              width: 30px;
              height: 30px;
            }
          }
        }
      }
    }
  }

  section#roadmaps {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
    background: #60f2fa8b;

    .roadmap {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1.5rem;
      padding: 1rem;

      a {
        background: #fff;
        padding: 1rem;
        border-radius: 10px;

        display: flex;
        flex-direction: column;
        gap: 0.5rem;

        color: #29325b;
        text-align: center;
        box-shadow: 2px 2px 10px #470cb562;

        img {
          width: 100%;
          max-width: 100px;
        }
      }
    }
  }
}

button#view-all-techs {
  margin-top: 2rem;
  margin-bottom: 2rem;
  max-width: fit-content;

  position: relative;
  left: 50%;
  transform: translateX(-50%);

  color: #00f2b6;
  background: #5c6db6cc;
  font-weight: 700;
  padding: 1rem 2rem;
  font-size: 1rem;

  border-radius: 50px;
  border: none;
  outline: none;

  cursor: pointer;

  &:hover {
    color: #29325b;
    background: #00f2b6;
  }
}
/* Hero Section */

section.hero {
  position: relative;
  top: 0;
  z-index: 9;
  text-align: center;

  background: radial-gradient(#2575fc, #7b00ff);
  // background: url("https://tse2.mm.bing.net/th?id=OIG4.gZw6M0.leGwHGq29pKo_&pid=ImgGn");
  // background: url("https://tse2.mm.bing.net/th?id=OIG1.EapVr78nRZhw2dovtg9L&pid=ImgGn");
  background: url("./../images/background.jpg");
  background-size: 100% cover;
  background-position: center;

  transition: 0.15s linear;

  color: #fff;
  padding: 0;

  height: 100dvh;
  max-height: 100dvh;
  z-index: 1;

  animation: backgroundScale 0.5s linear alternate;

  @keyframes backgroundScale {
    from {
      background-size: 100%;
    }

    to {
      background-size: 150%;
      background-position: center;
    }
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    // background: radial-gradient(#2574fc8d, #5b23f4ce, #2641c8);
    background: radial-gradient(#25d8fc8d, #35265fce, #29325b);
    backdrop-filter: blur(2px);
    // background: radial-gradient(#fbff1e5c, #a27131ce, #c8266c);

    width: 100%;
    height: 100%;

    padding: 1rem;

    h1 {
      font-size: 2.5rem;
      margin-bottom: 10px;

      filter: drop-shadow(2px 2px 2px #00000055);

      span {
        color: #00f2b6;
      }
    }

    p {
      font-size: 1.2rem;
      margin-bottom: 20px;

      filter: drop-shadow(2px 2px 2px #00000055);
    }

    div {
      display: flex;
      gap: 1rem;

      .cta-button {
        background: #fff;
        color: #2575fc;
        padding: 10px 20px;
        border-radius: 20px;
        font-size: 1rem;
        text-decoration: none;

        box-shadow: 0px 0px 10px #00f2ff;

        &.roadmap-btn {
          background: transparent;
          color: #fff;
          border: 2px solid #fff;
          backdrop-filter: blur(5px);

          &:hover {
            background: #2575fc;
            border: 2px solid #2575fc;
          }
        }

        &:hover {
          box-shadow: 0px 0px 20px 2px #8bf9ff;
        }
      }
    }

    .social {
      display: flex;
      flex-direction: column;
      // position: relative;

      position: absolute;
      bottom: 55px;
      z-index: 99;

      span {
        position: relative;
        display: flex;
        gap: 0.7rem;
        justify-content: center;
        margin-bottom: -10px;

        transition: 0.1s;

        a {
          color: #fff;
          width: 40px;
          height: 40px;
          border-radius: 50px;
          display: flex;
          align-items: center;
          justify-content: center;

          i {
            position: relative;
            font-size: 2rem;
          }

          &#github {
            &:hover {
              color: #000000;
              background: #fff;
              box-shadow: 0 0 15px #fff;
            }
          }
          &#linkedin {
            &:hover {
              color: #0076b5;
              background: #fff;
              border-radius: 10px;
              box-shadow: 0 0 15px #00d9ff;
            }
          }
          &#whatsapp {
            &:hover {
              color: #26d466;
              background: #fff;
              box-shadow: 0 0 15px #38ff81;
            }
          }
          &#instagram {
            &:hover {
              color: #fff;
              background: linear-gradient(
                to right bottom,
                #4e57d4,
                #b8029f,
                red,
                #f7c920
              );
              border-radius: 10px;
              box-shadow: 0 0 15px #ff008c;
            }
          }
        }
      }

      h2 {
        color: hsla(0, 0%, 100%, 0.895);
        position: relative;
        z-index: 9;
      }
    }
  }
}

li.loading {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 1rem;
  padding-bottom: 1.5rem !important;
  position: relative;
  overflow: hidden;
  cursor: pointer;

  &:after {
    content: "";
    background: linear-gradient(
      to bottom,
      #00000000,
      rgba(149, 149, 149, 0.368),
      #00000000
    );
    position: absolute;
    top: 0;
    left: 0;
    width: 200%;
    height: 70%;
    cursor: pointer;

    rotate: -40deg;
    animation: skeleton-loading 1s cubic-bezier(0.11, 0.84, 0.89, 0.11) infinite;
  }

  @keyframes skeleton-loading {
    0% {
      transform: translate(-25%, -250%);
    }

    100% {
      transform: translate(-25%, 200%);
    }
  }

  .h3 {
    width: 100%;
    min-width: 100%;
    background: rgb(217, 217, 217) !important;
    height: 25px;
    border-radius: 5px;
  }

  .img {
    width: 100%;
    min-width: 100%;
    background: rgb(181, 180, 180) !important;
    border-radius: 10px;
    aspect-ratio: 16 / 9;
  }

  .h1 {
    width: 100%;
    min-width: 100%;
    background: rgb(198, 198, 198) !important;
    height: 50px;
    border-radius: 5px;
  }

  .author_info {
    display: flex;
    gap: 0.5rem;
    justify-content: start;

    .img {
      min-width: 30px;
      width: 30px !important;
      max-width: 30px !important;
      background: rgb(162, 162, 162) !important;
      min-height: 30px;
      border-radius: 50%;
    }

    .p {
      min-width: calc(100% - 30px - 0.5rem);
      max-width: calc(100% - 30px - 0.5rem);
      background: rgba(200, 200, 200, 0.75) !important;
      height: 30px;
      border-radius: 10px;
    }
  }
}

@media screen and (width < 800px) {
  section.hero {
    .container {
      h1 {
        font-size: 1.9rem;
      }
    }
  }

  .effect {
    transform: translate(-1rem, -44%) !important;
  }

  section#technologies {
    h1 {
      font-size: 1.3rem;
    }

    div.heading-flex {
      flex-direction: column;
      align-items: start !important;
      gap: 1rem;

      span {
        width: 100%;
        input {
          min-width: 100% !important;
        }
      }
    }

    .tech {
      padding: 1rem;

      display: grid;
      grid-template-columns: 1fr 1fr !important;
      gap: 0.7rem !important;

      .language-box {
        padding: 0.6rem !important;
        padding-block: 1.5rem !important;

        img {
          width: 70px !important;
        }

        h3 {
          font-size: 0.8rem !important;
          margin-top: 0rem !important;
        }

        p {
          margin-top: 0.3rem;
          font-size: 0.6rem !important;
        }
      }
    }
  }

  section#resources {
    padding: 2rem !important;

    h1 {
      font-size: 1.2rem;

      i {
        font-size: 1.8rem;
      }
    }

    ul {
      margin-top: 3rem !important;
      gap: 2rem !important;

      li {
        a {
          display: flex;
          flex-direction: column;

          h3 {
            margin: 0;
            color: #8f8f8f;
            font-size: 1rem;
          }

          img.thumbnail {
            width: 95% !important;
            // max-width: 250px;

            position: relative;
            left: 50%;
            transform: translateX(-50%);
          }

          // .author_info {
          //   img {
          //   }
          // }
        }
      }
    }
  }

  section#roadmaps {
    .roadmap {
      display: grid;
      grid-template-columns: 1fr !important;
      gap: 1rem;

      a {
        padding: 1rem;
        border-radius: 10px;

        display: flex;
        flex-direction: column;
        gap: 0.5rem;
      }
    }
  }
}
